<template>
    <v-container class="pa-0" style="max-width: 100%; height: 100%;">

        <div v-if="dataIsReady">
            <router-view style="padding-bottom: 64px"></router-view>
            <v-bottom-navigation
                    style="z-index: 4 !important"
                    height="64px"
                    app
                    :value="bottomNav"
                    color="transparent"
            >
                <v-row style="height: 100%; max-width: 100vw">
                    <v-col class="text-center pa-0">
                        <v-btn
                                class="bottom-nav-button"
                                color="primary"
                                text
                                align="center"
                                value="ranking"
                                @click="redirectTo('rankingHome')"
                        >
                            <!--<span>Ranking</span>-->
                            <v-icon>mdi-crown</v-icon>
                        </v-btn>
                    </v-col>
                    <v-col class="text-center pa-0">
                        <v-btn
                                class="bottom-nav-button"
                                color="primary"
                                text
                                value="events"
                                @click="redirectTo('eventsHome')"
                        >
                            <!--<span>Eventos</span>-->
                            <v-icon>mdi-map-marker</v-icon>
                        </v-btn>
                    </v-col>
                    <v-col class="text-center pa-0">
                        <v-btn
                                class="bottom-nav-button"
                                color="primary"
                                text
                                value="search"
                                @click="redirectTo('searchHome')"
                        >
                            <!--<span>Explorar</span>-->
                            <v-icon>mdi-magnify</v-icon>
                        </v-btn>
                    </v-col>
                    <v-col class="text-center pa-0">
                        <v-btn
                                class="bottom-nav-button"
                                color="primary"
                                text
                                value="chats"
                                @click="redirectTo('chatHome')"
                        >
                            <!--<span>Chats</span>-->
                            <div style="position: relative">
                                <v-icon>mdi-message</v-icon>
                                <b style="background-color: #0ccfd1; position:absolute; font-size: 14px; padding-right: 7px; padding-left: 7px; padding-top: 2px !important; padding-bottom: 2px !important; border-radius: 50%; color: white; top: -10px; right: calc(50% - 20px)"
                                   v-show="'chats' == $route.meta.menu && getUnreadMessages > 0"> ! </b>
                                <b style="background-color: #00abba; position:absolute; font-size: 14px; padding-right: 7px; padding-left: 7px; padding-top: 2px !important; padding-bottom: 2px !important; border-radius: 50%; color: white; top: -10px; right: calc(50% - 20px)"
                                   v-show="'chats' != $route.meta.menu && getUnreadMessages > 0"> ! </b>
                            </div>
                        </v-btn>
                    </v-col>
                    <v-col class="text-center pa-0">
                        <v-btn
                                class="bottom-nav-button"
                                color="primary"
                                text
                                value="profile"
                                @click="redirectTo('profileHome')"
                        >
                            <!--<span>Perfil</span>-->
                            <v-icon>mdi-account</v-icon>
                        </v-btn>
                    </v-col>
                </v-row>


            </v-bottom-navigation>
        </div>
        <div id="loading" v-if="!dataIsReady">
            <v-progress-circular
                    :size="100"
                    :width="7"
                    color="primary"
                    indeterminate
                    style="margin-top: -50px"
            >
                <img style="width: 60px" src="/LogoWeBabble.png"/>
            </v-progress-circular>
        </div>

        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
                style="bottom: 76px"
        >
            {{ snackbarText }}
            <v-btn
                    color="primary"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>


</template>

<script>
    import {mapActions, mapGetters} from "vuex";

    export default {
        name: "Dashboard",
        data() {
            return {
                bottomNav: this.$route.meta.menu,
                dataIsReady: false,
                snackbarText: null,
                snackbar: false,
                closeText: null
            }
        },
        methods: {
            ...mapActions(['initAppInfo', 'getLoggedUser']),
            ...mapActions('permissions', ['checkForGeolocationPermission', 'checkForPushNotificationsPermission']),

            redirectTo: function (goal) {
                let self = this;
                if (!goal.startsWith(this.$route.meta.menu)) {
                    this.$router.push({name: goal});
                }
                self.checkForPushNotificationsPermission().catch((error) => {
                    self.snackbarText = error;
                    self.snackbar = true;
                });
            }

        },
        created() {
            this.bottomNav = this.$route.meta.menu;
            /*let self = this;
            let resultInitAppInfo = self.initAppInfo();

            resultInitAppInfo
                .then(() => {
                    self.dataReady = true;
                })
                .catch((error) => {
                    self.snackbarText = error;
                    self.snackbar = true;
                });*/

        },
        mounted() {
            let self = this;

            self.getLoggedUser().then(currentUser => {
                if (currentUser != null && currentUser != undefined) {
                    // user is logged in
                    let resultInitAppInfo = self.initAppInfo();

                    resultInitAppInfo
                        .then(async () => {
                            self.dataIsReady = true;
                            await self.checkForPushNotificationsPermission().catch((error) => {
                                self.snackbarText = error;
                                self.snackbar = true;
                            });
                            await self.checkForGeolocationPermission().catch((error) => {
                                self.snackbarText = error;
                                self.snackbar = true;
                            });
                        })
                        .catch((error) => {
                            self.snackbarText = error;
                            self.snackbar = true;
                        });
                } else {
                    self.$store.dispatch('resetAppState').then(() => {
                        self.$router.replace('home');
                    })
                        .catch((error) => {
                            self.snackbarText = error;
                            self.snackbar = true;
                        });

                    self.dataIsReady = true;
                    console.log("No user logged");
                }
            });
        },
        computed: {
            ...mapGetters('chats', ['getUnreadMessages'])
}
    }
</script>

<style scoped>
    .bottom-nav-button {
        padding: 0 !important;
        width: 100% !important;
        height: 100% !important;
        max-width: unset !important;
        min-width: unset !important;
    }
</style>
